import { Component, OnInit, NOT_FOUND_CHECK_ONLY_ELEMENT_INJECTOR } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormArray} from '@angular/forms';
import { Router } from '@angular/router';
import { RegisteredUsersAPIService } from '../registered-users-api/registered-users-api.service';
import { PersistenceService, StorageType} from 'angular-persistence';

@Component({
  selector: 'app-property-upload-form',
  templateUrl: './property-upload-form.component.html',
  styleUrls: ['./property-upload-form.component.css']
})
export class PropertyUploadFormComponent implements OnInit {
  UploadForm: FormGroup;
  showErrors = false;
  imageFiles: Array<File> = [];
  formData: any = new FormData();
  image_count = 0;
  form_number = 4;

  constructor(
    private fb: FormBuilder,
    private registeredUsersAPI: RegisteredUsersAPIService,
    private router: Router,
    private peristant_data: PersistenceService,
  ) { }

  ngOnInit() {
    this.UploadForm = this.fb.group({
      property_name: ['', Validators.required],
      title: ['', Validators.required],
      property_type: ['', Validators.required],
      rent_or_sale: ['', Validators.required],
      location: this.fb.group({
        county: ['', Validators.required],
        city_or_town: ['', Validators.required],
        estate_or_area_name: ['', Validators.required]
      }),
      number_of_bedrooms: ['', Validators.required],
      number_of_bathrooms: ['', Validators.required],
      description: ['', Validators.required],
      amount_to_be_paid: ['', Validators.required],
      property_pictures: this.fb.array([
        this.fb.control('', Validators.required),
        this.fb.control('', Validators.required),
        this.fb.control('', Validators.required),
        this.fb.control('', Validators.required)
      ])
    });
  }

  get property_name() { return this.UploadForm.get('property_name'); }
  get property_type() { return this.UploadForm.get('property_type'); }
  get title() { return this.UploadForm.get('title'); }
  get rent_or_sale() { return this.UploadForm.get('rent_or_sale'); }
  get number_of_bedrooms() { return this.UploadForm.get('number_of_bedrooms'); }
  get number_of_bathrooms() { return this.UploadForm.get('number_of_bathrooms'); }
  get description() { return this.UploadForm.get('description'); }
  get amount_to_be_paid() { return this.UploadForm.get('amount_to_be_paid'); }
  get county() { return this.UploadForm.get('location').get('county'); }
  get city_or_town() { return this.UploadForm.get('location').get('city_or_town'); }
  get estate_or_area_name() { return this.UploadForm.get('location').get('estate_or_area_name'); }
  get pictures() {
    return this.UploadForm.get('property_pictures') as FormArray;
  }

  addPictures() {
    this.pictures.push(this.fb.control(''));
  }

  getPicturesLength() {
    const arrLength = this.pictures.controls.length;
    // console.log(this.pictures.invalid);
    if (arrLength <= 20) {
      return true;
    } else {
      return false;
    }
  }

  onSubmit() {
    if (this.UploadForm.status === 'INVALID') {
      this.showErrors = true;
    } else {
    const formValue = this.UploadForm.value;

    const property_details = {
      'property_name': formValue['property_name'],
      'title': formValue['title'],
      'property_type': formValue['property_type'],
      'rent_or_sale': formValue['rent_or_sale'],
      'location': {
        'county': formValue['location']['county'],
        'city_or_town': formValue['location']['city_or_town'],
        'estate_or_area_name': formValue['location']['estate_or_area_name'],
      },
      'number_of_bedrooms': formValue['number_of_bedrooms'],
      'number_of_bathrooms': formValue['number_of_bathrooms'],
      'description': formValue['description'],
      'amount_to_be_paid': formValue['amount_to_be_paid'],
    };
    const property_details_Json = JSON.stringify(property_details);
    this.formData.append('property_details', property_details_Json);

    let uploader = this.peristant_data.get('user_data', StorageType.SESSION);
    uploader = JSON.stringify(uploader);

    this.formData.append('uploader', uploader);
    this.formData.append('image_count', this.image_count);

    if (this.imageFiles) {
      for (const image of this.imageFiles) {
        if (image !== undefined) {
          const image_name = `image${this.image_count}`;
          this.formData.append(image_name, <File>image, image.name);
          this.image_count++;
        }
      }
    }

    this.registeredUsersAPI.uploadNewProperty(this.formData).subscribe(results => {
      this.router.navigate(['/account/dashboard']);
    });

    }
  }

  fileSubmit(event) {
    this.imageFiles.push(<File>event.target.files[0]);
  }

  removePicture(i) {
    delete this.imageFiles[i];
    this.pictures.controls[i].reset();
  }

}
