import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { PersistenceService, StorageType} from 'angular-persistence';
import { Property } from '../property';
import { PropertyResourceService } from '../../property-api/property-resource.service';

@Component({
  selector: 'app-property-detail',
  templateUrl: './property-detail.component.html',
  styleUrls: ['./property-detail.component.css']
})
export class PropertyDetailComponent implements OnInit {
  property_id;
  property_details;
  property_pictures = [];
  show_contacts = false;
  property_management_details;
  slideIndex = 1;

  constructor(
    private APIService: PropertyResourceService,
    private router: Router,
    private peristant_data: PersistenceService
  ) { }

  ngOnInit() {
    this.property_id = this.peristant_data.get('property_id', StorageType.SESSION);
    this.APIService.getPropertyDetails(this.property_id).subscribe(results => {
      this.property_details = results;
      this.property_pictures = this.property_details['pictures'];
      this.property_management_details = this.property_details['pictures'];
    });
  }

  showContacts() {
    this.show_contacts = true;
  }

}
