import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams, HttpResponse } from '@angular/common/http';
import { Property } from '../property/property';
import { Observable } from 'rxjs/Observable';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { PersistenceService, StorageType} from 'angular-persistence';

@Injectable()
export class PropertyResourceService {
  private serverBaseUrl = 'http://165.22.116.96:8000/property/serializers/';
  // private serverBaseUrl = 'http://127.0.0.1:8000/property/serializers/';
  private nextUrl;

  constructor(
    private httpclient: HttpClient,
    private peristant_data: PersistenceService
  ) { }

  private searchResults = new BehaviorSubject<any>(this.peristant_data.get('search_results',
  StorageType.SESSION));
  searchResults$ = this.searchResults.asObservable();

  set SearchResults(value) {
    this.searchResults.next(value);
  }

  get SearchResults(): any {
    return this.searchResults.getValue();
  }


  set NextUrl(url) {
    this.nextUrl = url;
  }

  get NextUrl() {
    return this.nextUrl;
  }

  setHttpParams(params) {
    const httpParams = {};

    Object.keys(params).forEach(key => {
      if (params[key] !== '' && params[key] !== 'undefined'
      && params[key] !== null) {
        httpParams[key] = params[key];
      }

    });
    return httpParams;
  }

  getLatestProperty(): Observable<Property> {
    return this.httpclient.get<Property>(`${this.serverBaseUrl}latest`);
  }

  getSearchResults(search_params: any): Observable<Property> {
    return this.httpclient.get<Property>(`${this.serverBaseUrl}search`, {params: search_params});
  }

  getAll_Locations() {
    return this.httpclient.get(`${this.serverBaseUrl}locations/all`);
  }

  getPropertyDetails(property_id) {
    return this.httpclient.get(`${this.serverBaseUrl}details/${property_id}`);
  }

  getTopProperties() {

  }
}
