import { Component, OnInit } from '@angular/core';
import { PersistenceService, StorageType} from 'angular-persistence';
import { PropertyResourceService } from '../property-api/property-resource.service';

@Component({
  selector: 'app-search-results',
  templateUrl: './search-results.component.html',
  styleUrls: ['./search-results.component.css']
})
export class SearchResultsComponent implements OnInit {
  searchResults;

  constructor(
    private peristant_data: PersistenceService,
    private APIService: PropertyResourceService,
  ) { }

  ngOnInit() {
    this.APIService.searchResults$.subscribe(results => {
      this.searchResults = results;
      console.log(this.searchResults);
    });
  }

}
