import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { PropertyTypes } from '../property/property';
import { PropertyResourceService } from '../property-api/property-resource.service';
import { Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';

interface Choices {
  choice: Array<string>;
}

@Component({
  selector: 'app-search-form',
  templateUrl: './search-form.component.html',
  styleUrls: ['./search-form.component.css']
})

export class SearchFormComponent implements OnInit {
  public selectedPropertyType = PropertyTypes.Default;
  @Output() searchParams = new EventEmitter<any>();
  selected_city: string;
  selected_estate: string;
  city_choices;
  estate_choices;
  locations_data;
  show_more_filters = false;
  // dbresults;

  constructor(
    private APIService: PropertyResourceService,
    private router: Router,
  ) { }

  ngOnInit() {
    this.APIService.getAll_Locations().subscribe(results => {
      this.locations_data = results;
      const choice = [];
      for (const obj of this.locations_data) {
        choice.push(`${obj['city_or_town']}`);
        // ${obj['estate_or_area_name']}
      }
      this.city_choices = new Set(choice);
    });
  }

  showMoreFilters(value: boolean) {
    this.show_more_filters = value;
  }

  changeOptions() {

    if (this.selected_city !== 'undefined' && this.selected_city !== '') {
      const choice = [];
      for (const obj of this.locations_data) {
        if (this.selected_city === obj['city_or_town']) {
          choice.push(`${obj['estate_or_area_name']}`);
        }
      }
      this.estate_choices = choice;
    } else {
      const choice = [];
      for (const obj of this.locations_data) {
        choice.push(`${obj['estate_or_area_name']}`);
      }
      this.estate_choices = choice;
    }
  }

  public search(value: any) {
    this.searchParams.emit(value);
}
  public selectedApartmentOrHouse() {
    return this.selectedPropertyType === PropertyTypes.Apartment || this.selectedPropertyType === PropertyTypes.House;
  }

  public selectedLand() {
    return this.selectedPropertyType === PropertyTypes.Land;
  }
}
