import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import { PropertyResourceService } from './property-display/property-api/property-resource.service';
import { AuthAPIService } from './auth/auth-api/auth.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  isLoggedIn: boolean;

  constructor(
    private router: Router,
    private propertyAPIService: PropertyResourceService,
    private authAPIService: AuthAPIService
    ) {}

    ngOnInit() {
      this.authAPIService.isLoggedIn$.subscribe(results => {
        const isLoggedIn = results;
        if (isLoggedIn === undefined) {
          this.isLoggedIn = false;
        } else {
          this.isLoggedIn = isLoggedIn;
        }
      });
    }

    goToHome() {
      this.router.navigate(['/home']);
    }

    goToSignIn() {
      this.router.navigate(['/sign-in']);
    }

    logout() {
      this.authAPIService.logout();
    }

    goToUpload() {
      const nextUrl = '/account/upload';
      this.propertyAPIService.NextUrl = nextUrl;
      this.router.navigate([nextUrl]);
    }

}
