import { Component, OnInit } from '@angular/core';
import { PropertyResourceService } from '../property-api/property-resource.service';
import { Router } from '@angular/router';
import { Property } from '../property/property';
import { PersistenceService, StorageType} from 'angular-persistence';

@Component({
  selector: 'app-home-page-property-list',
  templateUrl: './home-page-property-list.component.html',
  styleUrls: ['./home-page-property-list.component.css']
})
export class HomePagePropertyListComponent implements OnInit {
  propertyList: Property;

  constructor(
    private APIService: PropertyResourceService,
    private router: Router,
    private peristant_data: PersistenceService
  ) { }

  ngOnInit() {
    this.APIService.getLatestProperty().subscribe(results => {
      this.propertyList = results;
    });
  }
}
