import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import {FormGroup, FormBuilder, Validators } from '@angular/forms';
import { AuthAPIService } from '../auth-api/auth.service';

@Component({
  selector: 'app-sign-up',
  templateUrl: './sign-up.component.html',
  styleUrls: ['./sign-up.component.css']
})
export class SignUpComponent implements OnInit {
  signUpForm: FormGroup;
  first_password: string;
  second_password: string;
  show_validation_error = false;
  error_message = `passwords don't match`;

  userData;
  loginErrors: any;

  constructor(
    private router: Router,
    private fb: FormBuilder,
    private authAPI: AuthAPIService
    ) { }

  ngOnInit() {
    this.signUpForm = this.fb.group({
      'username': ['', Validators.required],
      'first_name': ['', Validators.required],
      'last_name': ['', Validators.required],
      'email': [''],
      'password': ['', Validators.required],
      'confirm_password': ['', Validators.required],
    });
  }

  passwordNotConfirmed() {
    if ( this.first_password !== this.second_password) {
      return true;
    } else {
      return false;
    }
  }

  SubmitSignUp() {
    if (this.signUpForm.status !== 'INVALID') {
      if (this.signUpForm.value['password'] === this.signUpForm.value['confirm_password']) {
        const user_data = {
          'username': this.signUpForm.value['username'],
          'first_name': this.signUpForm.value['first_name'],
          'last_name': this.signUpForm.value['last_name'],
          'email': this.signUpForm.value['email'],
          'password': this.signUpForm.value['password']
        };
        this.authAPI.registerUser(user_data).subscribe(results => {
          this.userData = results;
          const token = this.userData.token.token;
          const user = this.userData.user;
          this.authAPI.is_LoggedIn = true;
          this.authAPI.performLogin(token, user);
        }, error => {
          this.loginErrors = error['error']['detail'];
        });
      }
    } else {
      this.show_validation_error = true;
    }
  }

  goToSignIn() {
    this.router.navigate(['/sign-in']);
  }

}
