import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams, HttpResponse } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { AuthAPIService } from '../../auth/auth-api/auth.service';

@Injectable({
  providedIn: 'root'
})
export class RegisteredUsersAPIService {
  private serverBaseUrl = 'http://165.22.116.96:8000/accounts/serializers/';

  constructor(
    private httpclient: HttpClient,
    private authAPI: AuthAPIService,
  ) { }

  uploadNewProperty(property_details) {
    const token = this.authAPI.getToken();
    const httpOptions = this.authAPI.createHeaders(token, 'multipart/form-data');
    const url = `${this.serverBaseUrl}property/upload`;
    return this.httpclient.post(url, property_details);
  }
}
