import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { ActivatedRoute, Router } from '@angular/router';
import { CookieService } from 'ngx-cookie-service';
import 'rxjs/add/operator/map';
import { PropertyResourceService } from '../../property-display/property-api/property-resource.service';
import { PersistenceService, StorageType} from 'angular-persistence';
import { Observable } from 'rxjs/Observable';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { of } from 'rxjs/observable/of';
import { catchError, map, tap } from 'rxjs/operators';

import {AuthLoginData} from './auth';
import { User } from './user';



@Injectable()
export class AuthAPIService {
   private baseUrl = 'http://165.22.116.96:8000/accounts/serializers/';
   private nextUrl;

  private isLoggedIn = new BehaviorSubject<any>(this.peristant_data.get('isLoggedIn', StorageType.SESSION)); // true is your initial value
  isLoggedIn$ = this.isLoggedIn.asObservable();

  set is_LoggedIn(value) {
    this.isLoggedIn.next(value);
  }

  get is_LoggedIn(): boolean {
    return this.isLoggedIn.getValue();
  }

  constructor(
    private cookieService: CookieService,
    private http: HttpClient,
    private router: Router,
    private route: ActivatedRoute,
    private propertyAPIService: PropertyResourceService,
    private peristant_data: PersistenceService
    ) {}

  createHeaders(token?: string, content_type?: string) {
    const type = content_type;

    const data = {
         'Content-Type': type ? type : 'application/json',
    };
    if (token) {
      data['Authorization'] = `JWT ${token}`;
    }
     const httpOptions = {
          headers: new HttpHeaders(data)
      };
     return httpOptions;
  }

  checkToken() {
    const jwttoken = this.peristant_data.get('jwttoken', StorageType.SESSION);
    if (jwttoken) {
      return true;
    } else {
      return false;
    }
    // return this.cookieService.check('jwttoken');
  }

  getToken() {
    const jwttoken = this.peristant_data.get('jwttoken', StorageType.SESSION);
    return jwttoken;
    // return this.cookieService.get('jwttoken');
  }

  getNextUrl() {
    this.nextUrl = this.propertyAPIService.NextUrl;
    return this.nextUrl;
  }

  performLogin(token, user_data, expires?: Date, msg?: string) {
    let expiryDate = null;
    if (expires) {
       expiryDate = expires;
    }
    //  this.cookieService.set('jwttoken', token, expiryDate, '/');  this.getNextUrl();
    this.peristant_data.set('jwttoken', token,
    {type: StorageType.SESSION});

    this.peristant_data.set('user_data', user_data,
    {type: StorageType.SESSION});

     const nextUrl = this.getNextUrl();
     if (nextUrl) {
       this.router.navigate([nextUrl]);
     } else {
       this.router.navigate(['/account/dashboard']);
     }
  }

  login(data: AuthLoginData): Observable<any> {
      const apiLoginEndpoint = `${this.baseUrl}auth`;

      return this.http.post(apiLoginEndpoint, data).map(response => {

        this.peristant_data.set('isLoggedIn', true, {type: StorageType.SESSION});
        return response;
      });
  }

  registerUser(user_data) {
    const apiRegisterEndpoint = `${this.baseUrl}register`;
    return this.http.post(apiRegisterEndpoint, user_data).map(response => {
      this.peristant_data.set('isLoggedIn', true, {type: StorageType.SESSION});
      return response;
    });
  }

  logout() {
    this.peristant_data.remove('isLoggedIn', StorageType.SESSION);
    this.peristant_data.remove('user_data', StorageType.SESSION);
    this.peristant_data.remove('jwttoken', StorageType.SESSION);
    this.is_LoggedIn = false;
    this.router.navigate(['/home']);
  }

}
