import { Component, OnInit, Input} from '@angular/core';
import { Router } from '@angular/router';
import { PersistenceService, StorageType} from 'angular-persistence';
import { Property } from '../property';
import { PropertyResourceService } from '../../property-api/property-resource.service';

@Component({
  selector: 'app-property-list',
  templateUrl: './property-list.component.html',
  styleUrls: ['./property-list.component.css']
})
export class PropertyListComponent implements OnInit {
  searchResults = [1, 2, 3, 4, 5, 6, 7, 8];
  @Input() propertyList: Property[];

  constructor(
    private APIService: PropertyResourceService,
    private router: Router,
    private peristant_data: PersistenceService
  ) { }

  ngOnInit() {
    this.propertyList = this.peristant_data.get('search_results', StorageType.SESSION);
  }

  GoToDetails(property_id) {
    this.peristant_data.set('property_id', property_id,
    {type: StorageType.SESSION});

    this.router.navigate(['/property/details']);
  }

}
