import { Component, OnInit, OnDestroy} from '@angular/core';
import { FormControl, FormGroup, Validators, NgForm } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthLoginData } from '../auth-api/auth';
import { AuthAPIService } from '../auth-api/auth.service';
import { User } from '../auth-api/user';

@Component({
  selector: 'app-sign-in',
  templateUrl: './sign-in.component.html',
  styleUrls: ['./sign-in.component.css']
})
export class SignInComponent implements OnInit, OnDestroy {
  userData;
  loginForm: FormGroup;
  usernameField: FormControl;
  passwordField: FormControl;
  authLoginSub: any;
  loginErrors: any;
  tokenExists = false;
  didLogin = false;

  constructor(
    private router: Router, private route: ActivatedRoute,
    private authAPI: AuthAPIService
  ) { }

  ngOnInit() {
    this.usernameField  = new FormControl('', [
                Validators.required,
                Validators.minLength(0),
                Validators.maxLength(280)
           ]);
    this.passwordField  = new FormControl('', [
                Validators.required,
                Validators.minLength(4),
                Validators.maxLength(280)
           ]);
    this.loginForm = new FormGroup({
        'usernameField': this.usernameField,
        'passwordField': this.passwordField
    });
    this.tokenExists = this.authAPI.checkToken();
  }

  goToSignUp() {
    this.router.navigate(['/sign-up']);
  }

  doLogin(data) {
    this.authLoginSub = this.authAPI.login(data).subscribe(results => {
      this.userData = results;
      const token = this.userData.token.token || null;
      const user_data = this.userData.user;
      this.authAPI.is_LoggedIn = true;
      this.didLogin = true;
      this.authAPI.performLogin(token, user_data);
     }, error => {
       this.loginErrors = error['error']['detail'];
     });
  }

  ngOnDestroy() {
   if (this.authLoginSub) {
     this.authLoginSub.unsubscribe();
   }
 }

  handleSubmit(event: any, ourLoginDir: NgForm, loginFormGroup: FormGroup) {
     event.preventDefault();
     if (ourLoginDir.submitted) {
         // interact with the server
         const authLoginData = new AuthLoginData(
             loginFormGroup['usernameField'],
             loginFormGroup['passwordField']
             );
         this.doLogin(authLoginData);
         ourLoginDir.resetForm({});
     }
 }
}
