import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import {LandingPageComponent} from '../property-display/landing-page/landing-page.component';
import {SignInComponent} from '../auth/sign-in/sign-in.component';
import {SignUpComponent} from '../auth/sign-up/sign-up.component';
import { SearchResultsComponent } from '../property-display/search-results/search-results.component';
import { PropertyDetailComponent } from '../property-display/property/property-detail/property-detail.component';
import { ContactFormComponent } from '../property-display/../contact-form/contact-form.component';
import { ContainerComponent } from '../property-display/container/container.component';
import { PropertyUploadFormComponent } from '../registered-users/property-upload-form/property-upload-form.component';
import { RegisteredUserContainerComponent } from '../registered-users/registered-user-container/registered-user-container.component';
import { DashboardComponent } from '../registered-users/dashboard/dashboard.component';
import { LoginRouteGuard } from './loginrouteguard';
import { DashboardHomeComponent } from '../registered-users/dashboard-home/dashboard-home.component';
import { UserProfileComponent } from '../registered-users/user-profile/user-profile.component';
// import { EditProfileComponent } from '../registered-users/edit-profile/edit-profile.component';
import { EditPropertyComponent } from '../registered-users/edit-property/edit-property.component';
import { HomePagePropertyListComponent } from '../property-display/home-page-property-list/home-page-property-list.component';

const appRoutes: Routes = [
  { path: '', pathMatch: 'full', redirectTo: ''},
  { path: '', component: ContainerComponent, children: [
    {path: '', component: LandingPageComponent, children: [
      {path: 'home', component: HomePagePropertyListComponent},
      {path: 'search/results', component: SearchResultsComponent},
      { path: '', pathMatch: 'full', redirectTo: 'home'},
    ]},
    {path: 'property/details', component: PropertyDetailComponent},
    { path: '', pathMatch: 'full', redirectTo: ''},
  ]},

  {path: 'sign-in', component: SignInComponent},
  {path: 'sign-up', component: SignUpComponent},
  {path: 'contact-us', component: ContactFormComponent},

  { path: 'account', component: RegisteredUserContainerComponent, canActivate: [LoginRouteGuard], children: [
    {path: '', component: DashboardComponent, children: [
      { path: '', pathMatch: 'full', redirectTo: 'dashboard'},
      {path: 'dashboard', component: DashboardHomeComponent},
      {path: 'profile', component: UserProfileComponent},
      {path: 'property/edit', component: EditPropertyComponent},
      // {path: 'profile/edit', component: EditProfileComponent},
      {path: 'upload', component: PropertyUploadFormComponent},
    ]},
  ]}
];

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forRoot(appRoutes)
  ],
  declarations: [],
  providers: [LoginRouteGuard],
  exports: [RouterModule]
})
export class AppRoutingModule { }
